import {Injectable, OnInit} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {StorageService} from "./storage.service";
@Injectable({
  providedIn: 'root'
})
export class CartService implements OnInit {

  items = [];
    addToCart(product) {
        var list =  this.storage.getItem("cartList");
        if (list)
            this.items = list;
        this.items.push(product);
        this.storage.setItem("cartList", this.items);
        console.log(this.storage.getItem("cartList"));
    }

    getItems() {
        return this.items;
    }

    clearCart() {
        this.items = [];
        this.storage.setItem("cartList", this.items);
        return this.items;
    }

    getShippingPrices() {
        return this.http.get('/assets/shipping.json');
    }

  constructor(
      private http: HttpClient,private storage:StorageService,
  ) { }

    ngOnInit(){
        var list =  this.storage.getItem("cartList");
        if (list){
            this.items = list;
        }
    }
}
